    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				act_info:[],
				memberInfo:[],
				act_id:0,
				money:0,
				num:1
			}				
		},
        computed: mapState(['hasLogin','userInfo','Location']),  
		onLoad(e) {
			let _this = this;
			_this.city = _this.Location.city_name;
			_this.act_id = e.act_id;
			console.log('页面加载');
			if (_this.hasLogin) { 
				_this.$Request.post('plat_act/act_join',{id:_this.act_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.act_info = res.datas.act_detail;
						_this.memberInfo = res.datas.member_info;
						_this.money = _this.act_info.act_price;
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						
					}
				 });
			}
		},
         onReachBottom() {
             console.log('正在加载中...');
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
         },
        methods: {
			toJoin(){
				let _this = this;
				console.log('页面加载');
				if (_this.hasLogin) { 
					_this.$Request.post('member_act/act_join',{id:_this.act_id,num:_this.num}).then(res => {
						if (res.code == 200) {
							console.log(res);
							if(res.datas.amount==0){
								uni.showToast({
									icon: 'none',
									title:'报名成功！',
									duration:3000,
									success: function () {
										 setTimeout(function () {
											uni.navigateTo({
												url:'myact',
											});
										 }, 2000);
									}
								});
							}else{
								_this.toPage('actpay?sn='+res.datas.pay_sn);
							}
							
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							
						}
					 });
				}
				
			},
			numberChange(data){
				console.log(data);
				let num_ = data.detail.value;
				this.num=num_;
				this.money = num_*this.act_info.act_price;
			},
            toPage: function(url) {
				if (!this.hasLogin) {
					url="/pages/member/login";
				} 
				uni.navigateTo({
					url:url,
				})
				
			},
        }  
    }  
